<template>
	<div class="content">
		<div class="all">
			<div class="left">
				<div class="main">
					<!-- 图片 -->
					<div class="pic">
						<img :src="coverImgUrl" alt="">
					</div>
					<!-- 标题/内容-->
					<div class="title">
						<div class="title1"> {{playListname}}</div>
						<div class="playlistname"><i class="el-icon-user"></i> {{ nickname }}</div>
						<div class="tip">标签:{{tags}}</div>
						<div class="on">播放量:{{playCount}}</div>
						<div class="like">收藏量:{{trackCount}}</div>
					</div>
				</div>
				<!-- 简介 -->
				<div class="introduce">
					<el-collapse>
						<el-collapse-item title="更多简介" name="1">
							<div>{{description}}</div>
						</el-collapse-item>
					</el-collapse>
				</div>
				<!-- 歌曲 -->
				<div class="songs">
					<el-table :data="tableData" style="width: 90%;margin-left: 50px;">
						<el-table-column label="歌曲标题" width="130" prop="name">
						</el-table-column>
						<el-table-column label="时长" width="100" prop="dt">
							<template slot-scope="scope">
								{{scope.row.dt/1000 | timeFormat}}
							</template>
						</el-table-column>
						<el-table-column label="歌手" width="100" prop="ar[0].name">
						</el-table-column>
						<el-table-column label="专辑" width="180"  prop="al.name">
						</el-table-column>
						<el-table-column prop="id" width="180">
							<template slot-scope="scope">
								<span v-if="scope.row.mv=='' || scope.row.mv==null">
									<i class="el-icon-video-play" @click="playOnMusic(scope.row.id)"></i>
								</span>
								<span v-else="">
									<i class="el-icon-video-play" @click="playOnMusic(scope.row.id)"></i>
									<i class="el-icon-s-ticket" @click="playMv(scope.row.mv)"></i>
								</span>
							</template>
						</el-table-column>
					</el-table>
				</div>
				<!-- 获取热门评论 -->
				<div class="info">
					<div class="commentstitle">精彩评论</div>
					<ul class="hot-main-content" v-for="item in hotComments">
						<div v-if="!!item.beReplied[0]">
							<div class="mainbox">
								<div id="box">
									<img class="hot-main-pics" :src="item.user.avatarUrl" alt="" />
								</div>
								<div class="box-content">
									<p class="hot-main-list"> {{item.user.nickname}}</p>
									<p class="hot-main-list1"> {{item.timeStr}}</p>
									<p class="hot-main-list2"> {{item.content}}</p>
									<p class="hot-main-list3">❤ {{item.likedCount}}</p>
										<div class="more-comments">
											回复：
											<span class="box-content1" >
												<p class="hot-main-list4"> {{item.beReplied[0] && item.beReplied[0].user.nickname}}</p>
												<p class="hot-main-list5"> {{item.beReplied[0] && item.beReplied[0].content}}</p>
											</span>
										</div>
								</div>
							</div>
						</div>
						<div v-else="">
							<div class="mainbox11">
								<div id="box">
									<img class="hot-main-pics" :src="item.user.avatarUrl" alt="" />
								</div>
								<div class="box-content">
									<p class="hot-main-list"> {{item.user.nickname}}</p>
									<p class="hot-main-list1"> {{item.timeStr}}</p>
									<p class="hot-main-list2"> {{item.content}}</p>
									<p class="hot-main-list3">❤ {{item.likedCount}}</p>
								</div>
							</div>
						</div>
					</ul>
				</div>
			</div>
			<!-- 热门歌单 -->
			<div class="right">
				<div class="intHot">
					相关热门歌单
				</div>

				<ul v-for="item in relatedList">
					<li>
						<div class="box">
							<!-- 相关热门歌单的图片 -->
							<div class="hotPic">
								<img class="hot-main-pics" :src="item.coverImgUrl" alt=""
									@click.prevent="findreletedList(item.id)">
							</div>
							<div class="hot-right">
								<!-- 热门歌单名称 -->
								<div class="hotname">
									<p @click="findreletedList(item.id)">{{item.name}}</p>
								</div>
								<!-- 热门歌单作者 -->
								<div class="hotman">
									<i class="el-icon-user"></i>{{item.creator.nickname}}
								</div>
							</div>
						</div>
					</li>
				</ul>
			</div>
			<el-footer class="el-footer">
				<div class="img" style="width: 100%;  "></div>
				<a href="#">
					<div class="mask">
						<audio style="width: 100%;" :src="playonMusic" controls="controls" autoplay="autoplay" @play="play"
							@pause="pause"></audio>
					</div>
				</a>
			</el-footer>
			<div class="onmv" v-show="isPlayMv">
				<i class="el-icon-close" @click="hide"></i>
				<video class="center-block" :src="mvUrl" controls="controls" autoplay="autoplay"></video>
			</div>
		</div>
	</div>
</template>

<script>
	import store from '../../store/index.js'
	export default {
		data() {
			return {
				playListId: this.$store.state.sheetId,
				coverImgUrl: [],
				playListname: '',
				tags: [],
				nickname: '',
				trackCount: '',
				playCount: '',
				description: '',
				activeNames: ['1'],
				relatedList: [],
				tableData: [],
				hotComments: [],
				playonMusic: '',
				drawer: false,
				direction: 'btt',
				//MV地址
				mvUrl: "",
				isPlayMv: false
			}
		},
		methods: {
			handleClose(done) {
				this.$confirm('确认关闭？')
					.then(_ => {
						done();
					})
					.catch(_ => {});
			},
			/* 所有歌单分类 */
			/* 获取歌单图片 */
			getPic() {
				var that = this;
				this.$axios.get("https://autumnfish.cn/playlist/detail?id=" + that.playListId)
					.then((response) => {
						that.coverImgUrl = response.data.playlist.coverImgUrl;
					}, (err) => {})
			},
			getplayListname() {
				var that = this;
				this.$axios.get("https://autumnfish.cn/playlist/detail?id=" + that.playListId)
					.then((response) => {
						that.playListname = response.data.playlist.name;
					}, (err) => {});
			},
			getnickname() {
				var that = this;
				this.$axios.get("https://autumnfish.cn/playlist/detail?id=" + that.playListId)
					.then((response) => {
						that.nickname = response.data.playlist.subscribers[0].nickname;
					}, (err) => {});
			},
			gettags() {
				var that = this;
				this.$axios.get("https://autumnfish.cn/playlist/detail?id=" + that.playListId)
					.then((response) => {
						that.tags = response.data.playlist.tags;
					}, (err) => {});
			},
			gettrackCount() {
				var that = this;
				this.$axios.get("https://autumnfish.cn/playlist/detail?id=" + that.playListId)
					.then((response) => {
						that.trackCount = response.data.playlist.trackCount;
					}, (err) => {});
			},
			getplayCount() {
				var that = this;
				this.$axios.get("https://autumnfish.cn/playlist/detail?id=" + that.playListId)
					.then((response) => {
						that.playCount = response.data.playlist.playCount;
					}, (err) => {});
			},
			getdescription() {
				var that = this;
				this.$axios.get("https://autumnfish.cn/playlist/detail?id=" + that.playListId)
					.then((response) => {
						that.description = response.data.playlist.description;
					}, (err) => {});
			},
			getRelatedList() {
				var that = this;
				this.$axios.get("https://autumnfish.cn/related/playlist?id=" + that.playListId)
					.then((response) => {
						that.relatedList = response.data.playlists;
					}, (err) => {});
			},
			getRelatedSongs() {
				var that = this;
				this.$axios.get("https://autumnfish.cn/playlist/track/all?limit=10&offset=1&id=" + that.playListId)
					.then((response) => {
						that.tableData = response.data.songs;
					}, (err) => {});
			},
			gethotComments() {
				var that = this;
				this.$axios.get("https://autumnfish.cn/comment/playlist?id=" + that.playListId)
					.then((response) => {
						that.hotComments = response.data.hotComments;
					}, (err) => {});
			},
			playOnMusic(id) {
				var that = this;

				this.$axios.get("https://autumnfish.cn/song/url?id=" + id)
					.then((response) => {
						that.playonMusic = response.data.data[0].url;
					}, (err) => {});
			},
			findreletedList(id) {
				this.$store.commit("findId", id);
				this.$router.push("playListDetail1");
			},
			play() {
				this.isPlaying = true;
			},
			pause() {
				this.isPlaying = false;
			},
			//获取视频资源
			playMv(mv) {
				var that = this;
				this.$axios.get("https://autumnfish.cn/mv/url?id=" + mv)
					.then((response) => {
						that.isPlayMv = true;
						that.mvUrl = response.data.data.url;
					}, (err) => {})
			},
			hide() {
				this.isPlayMv = false;
			}
		},
		mounted: function() {
			this.getPic();
			this.getplayListname();
			this.getnickname();
			this.gettags();
			this.gettrackCount();
			this.getplayCount();
			this.getdescription();
			this.getRelatedList();
			this.getRelatedSongs();
			this.gethotComments();
		}
	}
</script>

